import { ArSceneType, RangeDesc, lerp } from '@basic'
import Comps from '../comps'

import {
  MeshStandardMaterial, Mesh,
} from 'three'

let inited = false
let w1!: Mesh
let line1!: Mesh


let init: tickFunc = function (d, t, s) {
  if (inited) return
  w1 = s!.getObjectByName('w1') as Mesh
  line1 = s!.getObjectByName('line1') as Mesh
  inited = true
}

export default {
  sceneID: 'lunarpool',
  title: '月池型张紧索位置基准系统',
  sceneType: ArSceneType.model,
  desc: Comps.DescTest2,
  markerUrl: '6-12',
  size: 60,
  position: [0, 9.17, 0],
  children: [
    {
      model: 'lunar',
      position: [0, 9.17, 0],
      scale: [0.03, 0.03, 0.03],
      children:
        [
          {
            model: 'lunar_line',
            name: 'line1',
            position: [-45.26, 846.24, 128.56],
            scale: [40.0, 40.0, 40.0],
          },
          {
            model: 'lunar_weight',
            name: 'w1',
            position: [0.0, -322.19, -0.0],
          },
        ],
    },
  ],
  params: [
    {
      name: '升降',
      desc: RangeDesc.create({
        min: 0,
        max: 222,
        group: 5,
        unit: 'm'
      })
    }
  ],
  onTickFuncs: [
    init,
    (d, t, s) => {
      let n = s!.paramsValue.values['升降'] as number / 222
      w1.position.setY(lerp(1300, -322, n))
      line1.scale.set(0.2, lerp(0.4, 1, n), 0.2)
    }
  ]
} as modelSceneDesc
