import { ArSceneType, RangeDesc, toRad } from '@basic'
import Comps from '../comps'

import {
  MeshStandardMaterial, Mesh,
} from 'three'


let inited = false
let bb1!: Mesh
let bb2!: Mesh
let rr1!: Mesh
let rr2!: Mesh
let ee1!: Mesh
let ee2!: Mesh


let init: tickFunc = function (d, t, s) {
  if (inited) return
  bb1 = s!.getObjectByName('bb1') as Mesh
  bb2 = s!.getObjectByName('bb2') as Mesh
  rr1 = s!.getObjectByName('rr1') as Mesh
  rr2 = s!.getObjectByName('rr2') as Mesh
  ee1 = s!.getObjectByName('ee1') as Mesh
  ee2 = s!.getObjectByName('ee2') as Mesh
  inited = true
}

export default {
  sceneID: 'test3',
  title: '收放鳍',
  sceneType: ArSceneType.model,
  desc: Comps.DescTest2,
  markerUrl: '5-9',
  size: 395,
  position: [0, 21.3, 0],
  children: [
    {
      model: 'SM_BoatFinal_shou',
      position: [-23.3, 21.3, 0],
      scale: [1.99, 1.99, 1.99],
      children:
        [
          {
            model: 'body002',
            position: [13.55, -3.11, -10.93],
            rotate: [0.0, 90.0, 0.0],
            scale: [1.11, 1.11, 1.11],
            children:
              [
                {
                  model: 'r001',
                  name: 'bb1',
                  position: [3.99, 4.21, -5.9],
                  rotate: [0.0, -0.0, 180.0],
                  scale: [-1.0, -1.0, -1.0],
                  children:
                    [
                      {
                        model: 'ere001',
                        name: 'rr1',
                        position: [0.0, -2.49, -0.73],
                        rotate: [-90.0, -0.0, -0.0],
                        scale: [-0.66, -0.82, -0.82],
                        children:
                          [
                            {
                              model: 'SM_Wing_a001',
                              name: 'ee1',
                              position: [2.38, -4.69, -0.0],
                            },
                          ],
                      },
                    ],
                },
              ],
          },
          {
            model: 'body003',
            position: [13.55, -3.11, 10.92],
            rotate: [180.0, -90.0, 0.0],
            scale: [-1.11, -1.11, -1.11],
            children:
              [
                {
                  model: 'r002',
                  name: 'bb2',
                  position: [4.0, 4.21, -5.9],
                  rotate: [0.0, 180.0, 180.0],
                  scale: [-1.0, -1.0, -1.0],
                  children:
                    [
                      {
                        model: 'ere002',
                        name: 'rr2',
                        position: [0.0, -2.49, -0.0],
                        rotate: [-90.0, 0.0, -0.0],
                        scale: [0.66, 0.82, 0.82],
                        children:
                          [
                            {
                              model: 'SM_Winga002',
                              name: 'ee2',
                              position: [2.38, -4.69, 0.0],
                            },
                          ],
                      },
                    ],
                },
              ],
          },
        ],
    },
  ],
  params: [
    {
      name: '收放角',
      desc: RangeDesc.create({
        min: 0,
        max: 90,
        group: 5,
        unit: '°'
      })
    },
    {
      name: '翼角',
      desc: RangeDesc.create({
        min: -30,
        max: 30,
        group: 5,
        unit: '°'
      })
    }
  ],
  onTickFuncs: [
    init,
    (d, t, s) => {
      let m = toRad(s!.paramsValue.values['收放角'] as number)
      let n = toRad(s!.paramsValue.values['翼角'] as number)
      bb1.rotation.set(bb1.rotation.x, m, bb1.rotation.z)
      bb2.rotation.set(bb2.rotation.x, Math.PI + m, bb2.rotation.z)
      rr1.rotation.set(rr1.rotation.x, n, rr1.rotation.z)
      rr2.rotation.set(rr2.rotation.x, n, rr2.rotation.z)
      ee1.rotation.set(ee1.rotation.x, n, ee1.rotation.z)
      ee2.rotation.set(ee2.rotation.x, n, ee2.rotation.z)
    }
  ]
} as modelSceneDesc
